'use client';

import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ConnectionStatus, ConnectionInfo } from '@/lib/supabase/realtime';

export interface EnhancedConnectionStatusDisplayProps {
  status: ConnectionStatus;
  connectionInfo: ConnectionInfo;
  networkStatus?: {
    isOnline: boolean;
    isSlowConnection: boolean;
    connectionType: string | null;
  };
  batteryStatus?: {
    isCharging: boolean | null;
    level: number | null;
    chargingTime: number | null;
    dischargingTime: number | null;
  };
  isPageVisible?: boolean;
  smartReconnectEnabled?: boolean;
  batterySaverEnabled?: boolean;
  onManualReconnect?: () => void;
  onToggleSmartReconnect?: () => void;
  onToggleBatterySaver?: () => void;
  className?: string;
  showDetails?: boolean;
  showAdvancedControls?: boolean;
}

export function EnhancedConnectionStatusDisplay({
  status,
  connectionInfo,
  networkStatus,
  batteryStatus,
  isPageVisible = true,
  smartReconnectEnabled = true,
  batterySaverEnabled = true,
  onManualReconnect,
  onToggleSmartReconnect,
  onToggleBatterySaver,
  className = '',
  showDetails = false,
  showAdvancedControls = false
}: EnhancedConnectionStatusDisplayProps) {
  const [isExpanded, setIsExpanded] = useState(false);

  const getStatusConfig = () => {
    switch (status) {
      case 'connected':
        return {
          color: 'bg-green-500',
          textColor: 'text-green-700',
          bgColor: 'bg-green-50',
          borderColor: 'border-green-200',
          title: 'Ïã§ÏãúÍ∞Ñ Ïó∞Í≤∞Îê®',
          message: 'Ìà¨Ìëú Í≤∞Í≥ºÍ∞Ä Ïã§ÏãúÍ∞ÑÏúºÎ°ú ÏóÖÎç∞Ïù¥Ìä∏Îê©ÎãàÎã§.',
          icon: 'üü¢',
          showReconnect: false,
          severity: 'success' as const
        };
      case 'connecting':
        return {
          color: 'bg-yellow-500',
          textColor: 'text-yellow-700',
          bgColor: 'bg-yellow-50',
          borderColor: 'border-yellow-200',
          title: 'Ïó∞Í≤∞ Ï§ë...',
          message: 'Ïã§ÏãúÍ∞Ñ ÏóÖÎç∞Ïù¥Ìä∏Î•º ÏúÑÌï¥ ÏÑúÎ≤ÑÏóê Ïó∞Í≤∞ÌïòÍ≥† ÏûàÏäµÎãàÎã§.',
          icon: 'üü°',
          showReconnect: false,
          severity: 'warning' as const
        };
      case 'reconnecting':
        return {
          color: 'bg-blue-500',
          textColor: 'text-blue-700',
          bgColor: 'bg-blue-50',
          borderColor: 'border-blue-200',
          title: 'Ïû¨Ïó∞Í≤∞ Ï§ë...',
          message: `Ïû¨Ïó∞Í≤∞ÏùÑ ÏãúÎèÑÌïòÍ≥† ÏûàÏäµÎãàÎã§. (${connectionInfo.reconnectAttempts}/${connectionInfo.maxReconnectAttempts})`,
          icon: 'üîÑ',
          showReconnect: false,
          severity: 'info' as const
        };
      case 'disconnected':
        return {
          color: 'bg-gray-500',
          textColor: 'text-gray-700',
          bgColor: 'bg-gray-50',
          borderColor: 'border-gray-200',
          title: 'Ïó∞Í≤∞ ÎÅäÍπÄ',
          message: 'Ïã§ÏãúÍ∞Ñ ÏóÖÎç∞Ïù¥Ìä∏Í∞Ä ÏùºÏãúÏ†ÅÏúºÎ°ú Ï§ëÎã®ÎêòÏóàÏäµÎãàÎã§.',
          icon: '‚ö™',
          showReconnect: true,
          severity: 'neutral' as const
        };
      case 'network_error':
        return {
          color: 'bg-orange-500',
          textColor: 'text-orange-700',
          bgColor: 'bg-orange-50',
          borderColor: 'border-orange-200',
          title: 'ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò',
          message: networkStatus?.isOnline ? 
            'ÎÑ§Ìä∏ÏõåÌÅ¨ Ïó∞Í≤∞Ïù¥ Î∂àÏïàÏ†ïÌï©ÎãàÎã§.' : 
            'Ïù∏ÌÑ∞ÎÑ∑ Ïó∞Í≤∞ÏùÑ ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.',
          icon: 'üì°',
          showReconnect: networkStatus?.isOnline ?? true,
          severity: 'warning' as const
        };
      case 'suspended':
        return {
          color: 'bg-gray-400',
          textColor: 'text-gray-600',
          bgColor: 'bg-gray-50',
          borderColor: 'border-gray-200',
          title: 'Ïó∞Í≤∞ ÏùºÏãú Ï§ëÎã®',
          message: 'Î∞∞ÌÑ∞Î¶¨ Ï†àÏïΩÏùÑ ÏúÑÌï¥ Î∞±Í∑∏ÎùºÏö¥ÎìúÏóêÏÑú Ïó∞Í≤∞Ïù¥ ÏùºÏãú Ï§ëÎã®ÎêòÏóàÏäµÎãàÎã§.',
          icon: '‚è∏Ô∏è',
          showReconnect: false,
          severity: 'neutral' as const
        };
      case 'error':
        return {
          color: 'bg-red-500',
          textColor: 'text-red-700',
          bgColor: 'bg-red-50',
          borderColor: 'border-red-200',
          title: 'Ïó∞Í≤∞ Ïò§Î•ò',
          message: connectionInfo.lastError?.message || 'ÏÑúÎ≤Ñ Ïó∞Í≤∞Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.',
          icon: 'üî¥',
          showReconnect: true,
          severity: 'error' as const
        };
    }
  };

  const config = getStatusConfig();

  // Ïó∞Í≤∞ ÌíàÏßà ÌëúÏãú
  const getConnectionQuality = () => {
    if (!networkStatus) return null;

    if (!networkStatus.isOnline) return { level: 0, text: 'Ïò§ÌîÑÎùºÏù∏', color: 'text-red-500' };
    if (networkStatus.isSlowConnection) return { level: 1, text: 'ÎäêÎ¶º', color: 'text-orange-500' };
    
    switch (networkStatus.connectionType) {
      case '4g':
      case 'wifi':
        return { level: 3, text: 'Ï¢ãÏùå', color: 'text-green-500' };
      case '3g':
        return { level: 2, text: 'Î≥¥ÌÜµ', color: 'text-yellow-500' };
      case '2g':
      case 'slow-2g':
        return { level: 1, text: 'ÎäêÎ¶º', color: 'text-orange-500' };
      default:
        return { level: 2, text: 'Î≥¥ÌÜµ', color: 'text-gray-500' };
    }
  };

  const connectionQuality = getConnectionQuality();

  // Î∞∞ÌÑ∞Î¶¨ ÌëúÏãú
  const getBatteryDisplay = () => {
    if (!batteryStatus || batteryStatus.level === null) return null;

    const percentage = Math.round(batteryStatus.level * 100);
    const isLow = percentage < 15;
    const isCritical = percentage < 5;

    return {
      percentage,
      isCharging: batteryStatus.isCharging,
      isLow,
      isCritical,
      icon: batteryStatus.isCharging ? 'üîå' : (isCritical ? 'ü™´' : (isLow ? 'üîã' : 'üîã')),
      color: isCritical ? 'text-red-500' : (isLow ? 'text-orange-500' : 'text-green-500')
    };
  };

  const batteryDisplay = getBatteryDisplay();

  // Ïó∞Í≤∞ÎêòÏñ¥ ÏûàÍ≥† ÏÉÅÏÑ∏Ï†ïÎ≥¥Î•º Î≥¥ÏßÄ ÏïäÏúºÎ©¥ ÌëúÏãúÌïòÏßÄ ÏïäÏùå
  if (status === 'connected' && !showDetails && !showAdvancedControls) {
    return null;
  }

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: -20 }}
        className={`rounded-lg border ${config.borderColor} ${config.bgColor} p-4 ${className}`}
      >
        {/* Ìó§Îçî */}
        <div className="flex items-center justify-between">
          <div className="flex items-start space-x-3">
            {/* ÏÉÅÌÉú ÏïÑÏù¥ÏΩò */}
            <motion.div
              className={`w-4 h-4 rounded-full ${config.color} mt-0.5`}
              animate={
                status === 'connecting' || status === 'reconnecting' || status === 'error' ? {
                  scale: [1, 1.2, 1],
                  opacity: [1, 0.7, 1]
                } : {}
              }
              transition={{
                duration: 1,
                repeat: Infinity,
                ease: "easeInOut"
              }}
            />

            <div className="flex-1 min-w-0">
              {/* Ï†úÎ™©Í≥º ÌíàÏßà ÌëúÏãú */}
              <div className="flex items-center space-x-2">
                <h4 className={`text-sm font-medium ${config.textColor}`}>
                  {config.title}
                </h4>
                {connectionQuality && (
                  <span className={`text-xs px-2 py-0.5 rounded-full bg-white ${connectionQuality.color}`}>
                    {connectionQuality.text}
                  </span>
                )}
              </div>
              
              {/* Î©îÏãúÏßÄ */}
              <p className="text-xs text-gray-600 mt-1">
                {config.message}
              </p>
            </div>
          </div>

          {/* ÌôïÏû•/Ï∂ïÏÜå Î≤ÑÌäº (Í≥†Í∏â Í∏∞Îä•Ïù¥ ÏûàÏùÑ ÎïåÎßå) */}
          {(showDetails || showAdvancedControls) && (
            <button
              onClick={() => setIsExpanded(!isExpanded)}
              className="text-gray-400 hover:text-gray-600 transition-colors"
            >
              <motion.div
                animate={{ rotate: isExpanded ? 180 : 0 }}
                transition={{ duration: 0.2 }}
              >
                ‚åÑ
              </motion.div>
            </button>
          )}
        </div>

        {/* ÏÉÅÏÑ∏ Ï†ïÎ≥¥ ÏòÅÏó≠ */}
        <AnimatePresence>
          {isExpanded && (showDetails || showAdvancedControls) && (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
              className="mt-4 space-y-3"
            >
              {/* ÏãúÏä§ÌÖú ÏÉÅÌÉú */}
              {showDetails && (
                <div className="grid grid-cols-2 gap-4 text-xs">
                  {/* ÎÑ§Ìä∏ÏõåÌÅ¨ ÏÉÅÌÉú */}
                  <div className="space-y-1">
                    <div className="font-medium text-gray-700">ÎÑ§Ìä∏ÏõåÌÅ¨</div>
                    <div className="space-y-0.5">
                      <div className="flex justify-between">
                        <span>Ïò®ÎùºÏù∏:</span>
                        <span>{networkStatus?.isOnline ? '‚úÖ' : '‚ùå'}</span>
                      </div>
                      <div className="flex justify-between">
                        <span>Ïó∞Í≤∞ ÌÉÄÏûÖ:</span>
                        <span>{networkStatus?.connectionType || 'Ïïå Ïàò ÏóÜÏùå'}</span>
                      </div>
                      <div className="flex justify-between">
                        <span>ÎäêÎ¶∞ Ïó∞Í≤∞:</span>
                        <span>{networkStatus?.isSlowConnection ? '‚ö†Ô∏è' : '‚úÖ'}</span>
                      </div>
                    </div>
                  </div>

                  {/* Î∞∞ÌÑ∞Î¶¨ ÏÉÅÌÉú */}
                  <div className="space-y-1">
                    <div className="font-medium text-gray-700">Î∞∞ÌÑ∞Î¶¨</div>
                    {batteryDisplay ? (
                      <div className="space-y-0.5">
                        <div className="flex justify-between items-center">
                          <span>ÏûîÎüâ:</span>
                          <span className={`flex items-center space-x-1 ${batteryDisplay.color}`}>
                            <span>{batteryDisplay.icon}</span>
                            <span>{batteryDisplay.percentage}%</span>
                          </span>
                        </div>
                        <div className="flex justify-between">
                          <span>Ï∂©Ï†Ñ Ï§ë:</span>
                          <span>{batteryDisplay.isCharging ? 'üîå' : 'üîã'}</span>
                        </div>
                      </div>
                    ) : (
                      <div className="text-gray-500">ÏßÄÏõê ÏïàÎê®</div>
                    )}
                  </div>

                  {/* ÌéòÏù¥ÏßÄ ÏÉÅÌÉú */}
                  <div className="space-y-1">
                    <div className="font-medium text-gray-700">ÌéòÏù¥ÏßÄ</div>
                    <div className="space-y-0.5">
                      <div className="flex justify-between">
                        <span>ÌôúÏÑ± ÏÉÅÌÉú:</span>
                        <span>{isPageVisible ? 'üëÄ' : 'üôà'}</span>
                      </div>
                      {connectionInfo.lastConnected && (
                        <div className="flex justify-between">
                          <span>ÎßàÏßÄÎßâ Ïó∞Í≤∞:</span>
                          <span>{connectionInfo.lastConnected.toLocaleTimeString()}</span>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* Ïó∞Í≤∞ ÌÜµÍ≥Ñ */}
                  <div className="space-y-1">
                    <div className="font-medium text-gray-700">Ïó∞Í≤∞ Ï†ïÎ≥¥</div>
                    <div className="space-y-0.5">
                      <div className="flex justify-between">
                        <span>Ïû¨Ïó∞Í≤∞ ÌöüÏàò:</span>
                        <span>{connectionInfo.reconnectAttempts}/{connectionInfo.maxReconnectAttempts}</span>
                      </div>
                      {connectionInfo.nextReconnectAt && (
                        <div className="flex justify-between">
                          <span>Îã§Ïùå Ïû¨Ïó∞Í≤∞:</span>
                          <span>{connectionInfo.nextReconnectAt.toLocaleTimeString()}</span>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              )}

              {/* Ïò§Î•ò Ï†ïÎ≥¥ */}
              {connectionInfo.lastError && (
                <div className="p-2 bg-red-50 border border-red-200 rounded text-xs">
                  <div className="font-medium text-red-700">ÎßàÏßÄÎßâ Ïò§Î•ò:</div>
                  <div className="text-red-600 mt-1">
                    {connectionInfo.lastError.type}: {connectionInfo.lastError.message}
                  </div>
                </div>
              )}

              {/* Í≥†Í∏â Ï†úÏñ¥ */}
              {showAdvancedControls && (
                <div className="space-y-3 pt-2 border-t border-gray-200">
                  {/* ÌÜ†Í∏Ä Ïä§ÏúÑÏπòÎì§ */}
                  <div className="space-y-2">
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium">Ïä§ÎßàÌä∏ Ïû¨Ïó∞Í≤∞</span>
                      <button
                        onClick={onToggleSmartReconnect}
                        className={`w-8 h-4 rounded-full transition-colors ${
                          smartReconnectEnabled ? 'bg-blue-500' : 'bg-gray-300'
                        }`}
                      >
                        <motion.div
                          className="w-3 h-3 bg-white rounded-full shadow"
                          animate={{ x: smartReconnectEnabled ? 18 : 2 }}
                          transition={{ duration: 0.2 }}
                        />
                      </button>
                    </div>
                    
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium">Î∞∞ÌÑ∞Î¶¨ Ï†àÏïΩ</span>
                      <button
                        onClick={onToggleBatterySaver}
                        className={`w-8 h-4 rounded-full transition-colors ${
                          batterySaverEnabled ? 'bg-green-500' : 'bg-gray-300'
                        }`}
                      >
                        <motion.div
                          className="w-3 h-3 bg-white rounded-full shadow"
                          animate={{ x: batterySaverEnabled ? 18 : 2 }}
                          transition={{ duration: 0.2 }}
                        />
                      </button>
                    </div>
                  </div>

                  {/* Ïï°ÏÖò Î≤ÑÌäºÎì§ */}
                  <div className="flex space-x-2">
                    {config.showReconnect && onManualReconnect && (
                      <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        onClick={onManualReconnect}
                        className="flex-1 px-3 py-2 text-xs bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
                      >
                        ÏàòÎèô Ïû¨Ïó∞Í≤∞
                      </motion.button>
                    )}
                  </div>
                </div>
              )}
            </motion.div>
          )}
        </AnimatePresence>

        {/* Îπ†Î•∏ Ïï°ÏÖò (Ìï≠ÏÉÅ ÌëúÏãú) */}
        {!isExpanded && config.showReconnect && onManualReconnect && (
          <motion.button
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
            onClick={onManualReconnect}
            className="mt-3 w-full px-3 py-2 text-xs bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
          >
            Îã§Ïãú Ïó∞Í≤∞
          </motion.button>
        )}
      </motion.div>
    </AnimatePresence>
  );
}

// Í∏∞Ï°¥ ConnectionStatusDisplay (Ìò∏ÌôòÏÑ± Ïú†ÏßÄ)
export interface ConnectionStatusDisplayProps {
  status: ConnectionStatus;
  connectionInfo: ConnectionInfo;
  onManualReconnect?: () => void;
  className?: string;
  showDetails?: boolean;
}

export function ConnectionStatusDisplay(props: ConnectionStatusDisplayProps) {
  return <EnhancedConnectionStatusDisplay {...props} />;
}

// Í∞ÑÎã®Ìïú ÏÉÅÌÉú Ïù∏ÎîîÏºÄÏù¥ÌÑ∞ (Ìó§ÎçîÎÇò Ìë∏ÌÑ∞Ïö©)
export interface SimpleConnectionIndicatorProps {
  status: ConnectionStatus;
  networkStatus?: {
    isOnline: boolean;
    isSlowConnection: boolean;
    connectionType: string | null;
  };
  className?: string;
}

export function SimpleConnectionIndicator({
  status,
  networkStatus,
  className = ''
}: SimpleConnectionIndicatorProps) {
  if (status === 'connected' && networkStatus?.isOnline) {
    return null; // ÏôÑÏ†ÑÌûà Ï†ïÏÉÅÏù∏ ÏÉÅÌÉúÏóêÏÑúÎäî ÌëúÏãúÌïòÏßÄ ÏïäÏùå
  }

  const getIndicatorConfig = () => {
    // ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§ÌîÑÎùºÏù∏ ÏÉÅÌÉú Ïö∞ÏÑ† Ï≤òÎ¶¨
    if (networkStatus && !networkStatus.isOnline) {
      return { color: 'bg-red-400', text: 'Ïò§ÌîÑÎùºÏù∏', pulse: true };
    }

    switch (status) {
      case 'connecting':
      case 'reconnecting':
        return { color: 'bg-yellow-400', text: 'Ïó∞Í≤∞ Ï§ë', pulse: true };
      case 'network_error':
        return { color: 'bg-orange-400', text: 'ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò', pulse: true };
      case 'error':
        return { color: 'bg-red-400', text: 'Ïó∞Í≤∞ Ïò§Î•ò', pulse: true };
      case 'suspended':
        return { color: 'bg-gray-400', text: 'ÏùºÏãú Ï§ëÎã®', pulse: false };
      case 'connected':
        // Ïó∞Í≤∞ÎêòÏñ¥ ÏûàÏßÄÎßå ÎäêÎ¶∞ Ïó∞Í≤∞Ïù∏ Í≤ΩÏö∞
        if (networkStatus?.isSlowConnection) {
          return { color: 'bg-orange-400', text: 'ÎäêÎ¶∞ Ïó∞Í≤∞', pulse: false };
        }
        return { color: 'bg-green-400', text: 'Ïó∞Í≤∞Îê®', pulse: false };
      default:
        return { color: 'bg-gray-400', text: 'Ïó∞Í≤∞ ÏïàÎê®', pulse: false };
    }
  };

  const config = getIndicatorConfig();

  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.8 }}
      animate={{ opacity: 1, scale: 1 }}
      className={`flex items-center space-x-2 ${className}`}
    >
      <motion.div
        className={`w-2 h-2 rounded-full ${config.color}`}
        animate={config.pulse ? {
          scale: [1, 1.3, 1],
          opacity: [1, 0.6, 1]
        } : {}}
        transition={{
          duration: 1.2,
          repeat: config.pulse ? Infinity : 0,
          ease: "easeInOut"
        }}
      />
      <span className="text-xs text-gray-600">
        {config.text}
      </span>
    </motion.div>
  );
} 